import React,{useState,useEffect} from "react";
import { Link } from "react-router-dom";
import sanityClient from "../client"


export default function Project(){
    
    const [interviewsData,setInterviews] = useState(null);
    const url =decodeURI('*[_type == "post" && "Interviews" in categories[]->title]{mainImage{asset->{_id,url},alt},title,slug,"categoty":categories[0]->title,"text":body[]}');
    
    useEffect(()=>{
        sanityClient
        .fetch(url)
        .then((data)=>(
            setInterviews(data)
        ))
        .catch(console.error)
    });

    return (
        <main className="bg-green-100 min-h-screen p-1">
           <section className="container mx-auto">
               <h2 className="text-5xl flex justify-center cursive">Blog Posts page</h2>
               <h3 className="text-lg text-gray-600 flex justify-center mb-12">Welcome to my page of blog posts</h3>
               <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
                   {interviewsData 
                    && interviewsData.map((post,index)=>(
                        <article key={index}>
                        <Link to={"/post/" + post.slug.current} key={post.slug.current}>
                            <span 
                            className="block h-64 relative rounded shadow leading-snug bg-white border-l-8 border-green-400" 
                            >
                                <img src={post.mainImage.asset.url}
                                    alt={post.mainImage.alt}
                                    className="w-full h-full rounded-r object-cover absolute">

                                </img>
                                <span className="block relative h-full flex justify-end items-end pr-4 pb-4">
                                    <h2 className="text-gray-800 text-lg font-bold px-3 py-4 bg-red-700 text-red-100 bg-opacity-75 rounded">
                                        {post.title}
                                    </h2>
                                </span>
                            </span>
                        </Link>
                   </article>
                    ))}
               </div>
           </section>
       </main>
    );
}